//vue
import Vue from 'vue'
//
import Vuerouter from 'vue-router'
//element-ui message
import { Message } from 'element-ui'
//
Vue.use(Vuerouter);
//store
import store from '../store'

//push
let originPush = Vuerouter.prototype.push;
Vuerouter.prototype.push = function (location, resolve, reject) {
    //query|params
    //undefined|
    //:undefined|
    if (resolve && reject) {
        //push|
        //originPushcall(.$router)
        originPush.call(this, location, resolve, reject);
    } else {
        //push|
        originPush.call(
            this,
            location,
            () => { },
            () => { }
        );
    }
};

//
import Main from '../pages/Main'
import Audit from '../pages/Audit'
import Info from '../pages/Information'
import Login from '../pages/Login'
import JDBC from '../pages/Learning/JDBC'
import Mybatis from '../pages/Learning/Mybatis'
import Upload from '../pages/Learning/Upload'
import Traversial from '../pages/Learning/Traversal'
import SSRF from '../pages/Learning/SSRF'
import XEE from '../pages/Learning/XEE'
import RCE from '../pages/Learning/RCE'
import XSS from '../pages/Learning/XSS'
import Reverse from '../pages/Learning/Reverse'
//
let router = new Vuerouter({
    routes: [
        {
            path: '/main',
            component: Main
        },
        {
            path: '/audit',
            component: Audit
        },
        {
            path: '/info',
            component: Info
        },
        {
            path: '/login',
            component: Login
        },
        {
            path: '/learning/JDBC',
            name: 'JDBC',
            component: JDBC,
        },
        {
            path: '/Learning/Mybatis',
            name: 'Mybatis',
            component: Mybatis,
        },
        {
            path: '/Learning/traversial',
            name: 'Traversial',
            component: Traversial,
        },
        {
            path: '/Learning/upload',
            name: 'Upload',
            component: Upload,
        },
        {
            path: '/learning/ssrf',
            name: 'SSRF',
            component: SSRF,
        },
        {
            path: '/learning/rce',
            name: 'RCE',
            component: RCE,
        },
        {
            path: '/learning/xee',
            name: 'XEE',
            component: XEE,
        },
        {
            path: '/learning/xss',
            name: 'XSS',
            component: XSS,
        },
        {
            path: '/learning/reverse',
            name: 'reverse',
            component: Reverse,
        },
        {
            path: '*',
            redirect: '/main'
        }
    ],
})

//$message

router.beforeEach((to, from, next) => {
    let isLog = store.state.login.isLog;
    if (to.path == '/login' || to.path == '/audit' || to.path == '/info') {
        if (isLog) {
            if (to.path == '/login')
                Message.warning("");
            else
                next();
        } else {
            if (to.path == "/audit" || to.path == '/info')
                Message.warning("");
            else
                next();
        }
    } else
        next();

})

export default router;
